@charset "utf-8";

#wrapper{
    max-width:1500px;margin:0 auto;
}
.panel {
    width: 100%;display: flex;flex-direction: column;padding:50px;color: #fff;text-shadow:0 1px 5px rgba(0,0,0,1);
    background-repeat: no-repeat;background-size:cover;
    &.one{
        background-image: url(../images/1.jpg);height:100vh;
    }
    &.two{
        background-image: url(../images/2.jpg);@include vw-convert-pc(height, 999);
    }
    &.three{
        background-image: url(../images/3.jpg);@include vw-convert-pc(height, 1001);
    }
    &.four{
        background-image: url(../images/4.jpg);@include vw-convert-pc(height, 414);
    }
    &.five{
        background-image: url(../images/5.jpg);@include vw-convert-pc(height, 1028);
    }

    .title{
        span{
            display:block;
        }
        .sub_text{
            font-size:1em;
        }
        .main_text{
            font-size:2.5em;width:50%;
        }
    }
    .desc{
        padding-top:30px;
    }
}
